.fileInput-container input {
  display: none;
}

.fileInput-container label span {
  display: inline-block;
  padding: 10px 20px;
  border: 0;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 300;
  color: #333;
  background-color: #fbcdbb;
  outline: none;
  font-family: Montserrat;
  cursor: pointer;
  text-align: center;
}

.fileInput-container label span p {
  display: inline;
  margin-left: 5px;
}

.fileInput-container label p {
    margin: 5px;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 300;
}

.fileInput-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.fileInput-image img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    margin-left: 20px;
}

.fileInput-image {
    display: flex;
    align-items: center;
    justify-content: center;
}

.fileInput-notes {
    margin: 2px;
    margin-left: 10px;
    font-size: 12px;
    font-weight: 300;
    color: #333;
    font-family: Montserrat;
}

.flexCont {
    width: fit-content;
}

.fileInput-warning {
    color: #f44336;
    font-size: 12px;
    font-weight: 300;
    font-family: Montserrat;
    margin: 2px;
    margin-left: 10px;
}

@media screen and (max-width: 500px) {
  .fileInput-container {
    width: 300px;
  }
}