.settingsCont {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 40px;
}

.settingsCont .setting {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px 30px;
    width: 80%;
    border: 1px solid black;
    border-radius: 50px;
    margin-bottom: 30px;
    position: relative;
}

.settingsCont .setting h2 {
    font-size: 20px;
    font-weight: 400;
}

.settingsCont .setting .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.settingsCont .setting .switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.settingsCont .setting .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 34px;
}

.settingsCont .setting .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 50%;
}

.settingsCont .setting input:checked + .slider {
    background-color: #EE6730;
}

.settingsCont .setting input:focus + .slider {
    box-shadow: 0 0 1px #EE6730;
}

.settingsCont .setting input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.settingsCont .setting .disabledOverlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.settingsCont .logoutCont {
    width: 80%;
    clear: both;
    padding: 10px 0;
}

.settingsCont .logoutCont button {
    color: red;
    background-color: white;
    border: 1px solid red;
    transition: 0.1s;
    float: right;
}

.settingsCont .logoutCont button:hover {
    color: white;
    background-color: red;
}

.settingsCont .logoutCont button * {
    font-weight: 500 !important ;
}