.keyboardBtn {
    width: 70px;
    height: 70px;
    border: 1px solid var(--keyboardBtn_color);
    color: var(--keyboardBtn_color);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    transition: 0.3s;
    border-radius: 10px;
    user-select: none;
    cursor: default;
    transition: 0.1s all;
}

.keyboardBtn:hover {
    filter: brightness(0.8);
}

.keyboardBtn h2 {
    font-weight: 300;
}

.keyboardBtn__cont {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    flex-direction: column;
}

.keyboardBtn__cont p {
    margin: 5px;
    font-size: 11px;
    max-width: 70px;
}

.keyboardBtn__small {
    width: 30px;
    height: 30px;
    font-size: 10px;
    border-radius: 5px;
    margin: 0 5px;
}

.keyboardBtn__gray {
    color: #d9d9d9;
    border: 1px solid #d9d9d9;
}

.keyboardBtn__darkGray {
    color: #313131;
    border: 1px solid #313131;
    cursor: default !important;
}

.keyboardBtn__darkGray:hover {
    filter: none !important;
}

@media screen and (max-width: 430px) {
    .keyboardBtn__small {
        margin: 0 2px;
        width: 20px;
        height: 20px;
        font-size: 7px;
    }
    .keyboardBtn__cont {
        margin: 2px;
    }
}
