.new-tournament-3__container {
    min-height: 100%;
    min-width: 100%;
}

.new-tournament-3 {
    display: flex;
    padding: 30px;
    min-width: 100%;
    min-height: calc(100vh - 70px);
    height: auto;
}

.table__container {
    display: flex;
    width: 100%;
    margin-top: 50px;
    padding-right: 30px;
}

.table__container p {
    max-width: 40%;
    max-width: 200px;
    font-weight: 300;
    margin-left: 10px;
}

.refereeNum__container {
    margin-top: 40px;
}

.refereeOuter {
    height: 20px;
    width: 20px;
    border-radius: 5px;
    background-color: #727272;
    position: relative;
    display: block;
}

.refereeInner {
    height: 13px;
    width: 13px;
    border-radius: 3px;
    background-color: #EE6730;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media screen and (max-width: 1050px) {
    .new-tournament-3 {
        flex-direction: column;
    }
}

@media screen and (max-width: 650px) {
    .new-tournament-3 {
        padding: 20px 10px;
    }

    .new-tournament-3 .table__container {
        flex-direction: column;
    }

    .new-tournament-3 .table__container p {
        max-width: 100%;
        margin: 10px 0;
        margin-left: 10px;
    }
}


@media screen and (max-width: 450px) {
    .addTeam .teamSubmitCont {
        position: absolute;
        bottom: 30px;
        width: 90%;
    }
}