.tournamentStats {
    margin: 15px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
    min-height: 100%;
}

.tournamentStats .stickyContainer {
    position: sticky;
    top: 20px;
    margin-right: 5px;
}

.tournamentStats .statCardContainer {
    max-width: 70% !important;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
}

/* STAT CARD */

.tournamentStats .statCard {
    border: 1px solid #000;
    border-radius: 50px;
    padding: 10px 20px;
    margin: 10px;
    width: 350px;
    min-height: 200px;
}

.tournamentStats .statCard h2 {
    text-align: center;
    font-weight: 600;
    margin-top: 0;
}

.statCard .listContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 90%;
    margin: auto;
    margin-bottom: 30px;
    line-height: 1.3;
}

.statCard .listContainer ul {
    list-style: none;
    padding: 0;
    margin: 0;
    padding-right: 30px;
    font-weight: 300;
}

.statCard .listContainer ol {
    font-weight: 300;
}


@media screen and (max-width: 1300px) {
    .tournamentStats {
        max-width: 100%;
    }
}

@media screen and (max-width: 1060px) {
    .tournamentStats .stickyContainer {
        position: relative;
        margin-left: 0;
        margin-bottom: 50px;
        padding: 0;
        margin-top: 0;
    }
    .tournamentStats {
        flex-wrap: wrap;
        padding: 0;
    }
    .tournamentStats .statCardContainer {
        max-width: 100% !important;
    }
    .tournamentStats .descriptionCard {
        display: none;
    }
}