/* !! */
:root {
    --keyboardBtn_color: #1b7ced;
}


/* ! */
.flexStart {
    display: flex;
    justify-content: flex-start;
}
.spaceAround {
    display: flex;
    justify-content: space-around;
}
.center {
    display: flex;
    justify-content: center;
}
.verticalStart {
    display: flex;
    align-items: flex-start;
}
/* ! */

.verticalNums {
    margin: 0;
    justify-content: flex-start;
    align-items: flex-start;
}

.verticalNums__cont {
    align-self: center;
    display: inline-block;
    margin-left: 10px;
}

span.large-0 {
    width: 70px;
    height: 30px;
    display: inline-block;
    color: var(--keyboardBtn_color);
    border: 1px solid var(--keyboardBtn_color);
    border-radius: 5px;
    margin: 5px;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainBtns div {
    margin: 5px;
}

.points{
    max-width: 75%;
    margin: 0 auto;
}

.spaceBtn__cont {
    width: 400px;
    margin: 10px auto 40px;
}

.spaceBtn {
    height: 70px;
    width: 100%;
    border: 1px solid var(--keyboardBtn_color);
    color: var(--keyboardBtn_color);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    transition: 0.3s;
    border-radius: 10px;
    user-select: none;
    cursor: default;
}

.spaceBtn__cont p {
    margin: 5px;
    font-size: 11px;
}

.spaceBtn p {
    font-weight: 400;
    font-size: 20px;
}

.keyboard__readyBtn div {
    position: fixed;
    bottom: 30px;
    right: 30px;
}

.keyboard__readyBtn p {
    font-size: 20px !important;
}

.points .hiddenH2 {
    display: none;
}

.keyboard__container hr {
    max-width: 600px;
    min-width: 200px;
    width: 70%;
    background-color: var(--primary-text-color);
    height: 1px;
    border: none;
}

/* TODO: RESPONSIVE DESIGN */
@media screen and (max-width: 1200px) {
    .keyboard__container .horizontalCont {
        flex-direction: row;
    }
    .points {
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }

    .points .flexCont {
        margin: 20px;
    }

    .points {
        margin-top: 20px;
        flex-direction: column !important;
        text-align: center;
    }

    .points .hiddenH2 {
        display: block;
        text-align: center;
        margin: 5px auto;
    }
    .mainBtns_cont {
        flex-direction: column !important;
    }
    
}

@media screen and (max-width: 700px){
    .keyboard__readyBtn {
        position: relative;
        display: block;
        height: 50px;
        width: 100%;
        margin-bottom: 30px;
    }
    .keyboard__readyBtn div {
        left: 50vw;
        transform: translateX(-50%);
        position: absolute;
        top: 0;
    }
    .points {
        width: 100%;
        max-width: 100%;
        margin: 20px 0 0;
    }
}

@media screen and (max-width: 430px){
    .spaceBtn__cont {
        width: 300px;
    }
    .large-0 {
        width: 50px !important;
        height: 20px !important;
        margin: 2px !important;
        font-size: 10px;
    }
    .horizontalCont {
        flex-wrap: wrap;
        justify-content: center;
    }
}